@use './mixins' as *;

footer{
  position: sticky;
  bottom: 10px;
  overflow-x: hidden;

  .link{
    color:  #512e2d;
    width: 1.6rem;
    height: auto;
    
    &:hover{
      color: #E89675;
      transform: scale(1.1);
    }
  }

  #copyright{
    @include dancing-script;
    font-size: 0.8rem;
    color:  #512e2d;
    font-weight: bold;

  }
}
